.mfp-wrap{z-index:1043;outline:0!important;-webkit-backface-visibility:hidden}
.mfp-container{text-align:center;position:absolute;left:0;top:0;padding:0 8px;box-sizing:border-box}
.mfp-container:before{display:inline-block;height:100%;vertical-align:middle}
.mfp-align-top .mfp-container:before{display:none}
.mfp-content{position:relative;display:inline-block;vertical-align:middle;margin:0 auto;text-align:left;z-index:1045}
.mfp-ajax-holder .mfp-content,.mfp-inline-holder .mfp-content{width:100%;cursor:auto}
.mfp-ajax-cur{cursor:progress}
.mfp-zoom-out-cur,.mfp-zoom-out-cur .mfp-image-holder .mfp-close{cursor:-moz-zoom-out;cursor:-webkit-zoom-out;cursor:zoom-out}
.mfp-zoom{cursor:pointer;cursor:-webkit-zoom-in;cursor:-moz-zoom-in;cursor:zoom-in}
.mfp-auto-cursor .mfp-content{cursor:auto}
.mfp-arrow,.mfp-close,.mfp-counter,.mfp-preloader{-webkit-user-select:none;-moz-user-select:none;user-select:none}
.mfp-loading.mfp-figure{display:none}
.mfp-hide{display:none!important}
.mfp-preloader{color:#CCC;position:absolute;top:50%;left:50%;width:24px;height:24px;background:url(../assets/images/preloader-dark.gif) center center no-repeat;text-align:center;margin-top:-12px;margin-left:-12px;z-index:1044;text-indent:-9999px}
.mfp-preloader a{color:#CCC}
.mfp-close,.mfp-preloader a:hover{color:#FFF}
.mfp-s-error .mfp-content,.mfp-s-ready .mfp-preloader{display:none}
