@media (max-width:991px) {
    #primary-menu {
        display: block;
        float: none
    }
    #primary-menu-trigger {
        opacity: 1;
        pointer-events: auto;
        top: 25px;
        margin-top: 0;
        left: 0;
        z-index: 1
    }
    #primary-menu .container #primary-menu-trigger {
        top: 5px;
        left: 0
    }
    #primary-menu>div>ul,
    #primary-menu>ul {
        display: none;
        float: none!important;
        border: 0!important;
        padding: 0!important;
        margin: 0!important;
        -webkit-transition: none;
        -o-transition: none;
        transition: none
    }
    #primary-menu>div>ul {
        margin-top: 60px!important
    }
}

.opensource-stars {
    margin: 8px 0;
}